.btn_bar_wrapper {
  width: 20vw;
  height: 5.5vh;

  background-color: rgba(255, 255, 255, 0.6);
  border-radius: 10px;

  position: absolute;
  bottom: 0.1%;
  left: 60%;
  transform: translate(-50%, -50%);

  display: flex;
  justify-content: center;
  align-items: center;
}

.btn_bar {
  width: 90%;
  height: 90%;

  display: flex;
  justify-content: space-around;
  align-items: center;

  button {
    all: unset;
    width: 4.5vh;
    height: 4vh;
    padding: 0 2%;
    cursor: pointer;

    img {
      width: 100%;
      height: 100%;
    }
  }
}
